@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.app_content {
  margin: 40px auto;
  padding: 20px;
}

* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

.navbar {
  padding-top: 1.5%;

  padding-right: 2.5%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto;
  border-bottom: 1px solid gray;
  background-image: url(/elwinj.io/home/static/media/binaryRain.77cb70fc.gif);
}

.navbar h1 {
  padding-left: 2.5%;
  color: white;
  font-size: 45px;
}

.navbar .links {
  margin-left: auto;
  font-size: 19px;
  background-color: aqua;
}

.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.navbar a:hover {
  color: white;
}

.banner_bottom {
  position: fixed;
  padding-left: 0%;
  bottom: 0;
  width: 100%;
  height: 80px;
  margin: 0 auto;
  background-image: url(/elwinj.io/home/static/media/binaryRain.77cb70fc.gif);
}

.home_content {
  -webkit-align-items: center;
          align-items: center;
}

.about-container p {
  line-height: 15px;
  margin: 15px 0;
}

.about-container a {
  color: cyan;
}

.about_paragraph p {
  padding-left: 10%;
  padding-right: 10%;
  line-height: 1.6;
}

/* --------------Main Project page CSS--------------*/

/* Primary Container*/
.project-container {
  margin: 10px;
  transition: -webkit-transform 200ms;
  transition: transform 200ms;
  transition: transform 200ms, -webkit-transform 200ms;
}

/* Hover transfomration effect on html elements within primary container*/
.project-container :hover {
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
}

/* --------------Modal CSS-------------- */

/* Close/Exit Button */
.project-modal button {
  margin-left: 90%;
  padding: 0 50px;
}

/* Main Modal Heading */
.project-modal h1 {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  object-fit: contain;
}

/* Main Modal Paragraph Description*/
.p_description {
  width: 50%;
  margin-bottom: 1%;
  margin-left: 23%;
}

/* Project's link positioning*/
.projectLink {
  margin-left: 45%;
  color: cyan;
}

/* Primary container to place html elements into rows*/
.project-modal_info {
  display: -webkit-flex;
  display: flex;
}

/* Technologies Heading position within secondary container */
.project-modal_info_tech h2 {
  margin-left: 200%;
  font-size: 30px;
}

/* Technologies listed position within secondary container*/
.project-modal_info_tech p {
  margin-left: 150%;
  inline-size: 10px;
  font-size: 25px;
}

.projects_container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;

  transition: -webkit-transform 450ms;

  transition: transform 450ms;

  transition: transform 450ms, -webkit-transform 450ms;
  line-height: 15px;
}

.projects h3 {
  color: blue;
}

.projects_container :hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

.form_name-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 1%;
}

.form_email-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.form_subject-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.submitbutton {
  background-color: aqua;
  color: black;
  border: none;
  border-radius: 0.5vw;
  font-size: large;
}

.submitbutton:hover {
  background-color: black;
  color: aqua;
}

